  margin: 0;
  padding: 0;
  font-family: 'Orbitron', sans-serif;
  font-weight: bolder;
  font-size: 1.8rem;
  color: #300707;
  background: linear-gradient(to bottom, #b97979 0%, #864646 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; }
  body button i {
    color: #300707; }

#time-left {
  font-family: "Digital Counter 7", sans-serif;
  font-size: 3rem; }

p#time-left {
  border: 4px solid #300707;
  border-radius: 5px;
  color: #cb9b9b;
  background-color: #864646; }
  p#time-left.time-left {
    color: #8fdd8f;
    background-color: #0e2c0e; }
  p#time-left.one-minute {
    color: #eb8484;
    background-color: #cc0000; }
  p#time-left:after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-clip-path: polygon(0% 100%, 0% calc(100% - 5px), 100% calc(100% - 5px), 100% 100%);
            clip-path: polygon(0% 100%, 0% calc(100% - 5px), 100% calc(100% - 5px), 100% 100%);
    -webkit-animation: move 2s linear infinite;
            animation: move 2s linear infinite; }
  p#time-left.time-left:after {
    background: #8fdd8f; }
  p#time-left.one-minute:after {
    background: #eb8484; }
  p#time-left.paused:after {
    -webkit-clip-path: polygon(50% 100%, 50% 100%, 50% calc(100% - 5px), 50% calc(100% - 5px), 50% 100%);
            clip-path: polygon(50% 100%, 50% 100%, 50% calc(100% - 5px), 50% calc(100% - 5px), 50% 100%);
    -webkit-animation: none;
            animation: none; }

